.list_film {
  @media screen and (max-width: 767px) {
    padding-top: 20px;
  }

  @media screen and (min-width: 768px) {
    display: flex;
    flex-wrap: wrap;
    margin: -15px;
    padding-top: 60px;
  }
}

.list_film_image {
  cursor: pointer;
}

.list_film_item {
  @media screen and (max-width: 767px) {
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  @media screen and (min-width: 768px) {
    margin: 15px;
    width: calc((100% - 4 * 15px) / 2);
  }
  @media screen and (min-width: 1024px) {
    margin: 15px;
    width: calc((100% - 6 * 15px) / 3);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }
}

.list_film_item:hover,
.list_film_item:focus {
  transform: scale(1.04);
  // box-shadow: 0px 10px 30px rgba(100, 100, 100);
}
.info {
  margin-top: 10px;
}

.info-title {
  text-transform: uppercase;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
}

.info-date {
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  color: var(--accent-color);
}

.info-average {
  margin-left: 10px;
  width: 36px;
  height: 16px;
  background-color: #ff6b01;
  color: #ffffff;
  border-radius: 5px;
  display: inline-block;
  text-align: center;
}
